@import '../includes';

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: $header-height;
    padding-left: max(0rem, (100% - 500px) / 2);
    padding-right: max(0rem, (100% - 500px) / 2);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;

    .logo {
        margin: 0 0.5rem 0 1rem;
        font-size: 1.3rem;
        font-weight: normal;
        letter-spacing: -0.3px;
        color: $color-white;

        a {
            text-decoration: none;
            color:$color-white;
        }

        span {
            display: inline-block;
            margin-left: 2px;
        }
    }

    .settings-button {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 19px;
        width: 25px;
        padding: 0;
        margin: 12px 16px 12px 12px;
        float: right;
        border: none;
        background: transparent;
        position: relative;
        cursor: pointer;

        .bar {
            width: 100%;
            height: 3px;
            border-radius: 3px;
            background-color:$color-white;
        }

        .settings-open &::before {
            content: 'X';
            color: white;
            font-size: 1.7rem;
            width: 100%;
            line-height: 0.8;
            transform: scaleY(0.8);
        }

        .settings-open & .bar {
            display: none;
        }
    }
}