.settings-popup{display:none;width:100%;height:100%;border-top:60px solid rgba(0,0,0,.5);background:#0f0f0f;position:fixed;top:0;left:0;z-index:9;overflow:auto}.settings-open .settings-popup{display:block}.setting{display:none;max-width:500px;width:100%;height:calc(100% - 60px);border-top:60px solid var(--color-black);padding:2rem 1rem 2rem;margin:auto;border:none;background:#0f0f0f;position:fixed;top:60px;left:0;right:0;overflow:auto}.setting[data-show=true]{display:block;z-index:999}.setting legend{padding:0;margin-bottom:1.5rem;font-size:2rem;font-weight:600;text-transform:uppercase;position:relative}.setting .sub-menu-title{display:block;margin:.5rem 0;font-size:1rem}.setting legend .chevron{display:inline-block;width:20px;height:19px;position:relative}.setting legend .chevron::before{content:"›";display:flex;align-items:center;justify-content:center;height:19px;position:absolute;left:0;right:0;margin:auto}.setting legend .back-button{margin:0;font-size:.875rem;text-transform:none;font-weight:300;position:absolute;right:0;top:0}.setting-group{display:flex;align-items:center;justify-content:space-between;padding:.75rem .5rem;border-bottom:1px solid var(--color-black-border)}.setting-group label{display:inline-block;width:60px;font-size:1.125rem;padding-right:20px}.close-settings,.back-button{display:block;width:5rem;padding:.5rem 1rem;margin:2rem 0 0;border-radius:6px;font-size:.875rem;text-align:center;background-color:var(--color-red);cursor:pointer}.chevron-back::before{content:"‹";display:inline-block;font-size:1.5rem;line-height:0;transform:translateY(2px) translateX(-7px)}.rest-amount,.bar-weight{display:block;width:4rem;padding:4px 0rem;margin:0;border:none;outline:none;border-radius:.4rem;background-color:#fff;color:var(--color-black);font-size:1rem;font-family:sans-serif;font-weight:bold;text-align:center}.weight-select{display:block;padding:.2rem .5rem;margin:0;border:none;outline:none;border-radius:.4rem;font-size:1rem;font-family:sans-serif;font-weight:bold}.settings-list{max-width:500px;width:100%;padding:2rem 1rem;margin:auto}.menu-title{margin:0 0 1rem;font-size:2rem;font-weight:600;text-transform:uppercase}.setting-option{display:flex;align-items:center;width:100%;padding:1rem .5rem;border-bottom:1px solid var(--color-black-border);position:relative;cursor:pointer}.setting-option .label{margin-right:auto;font-size:1.125rem}.setting-option .preview{min-width:4rem;padding:.25rem 1rem;border-radius:6px;background-color:var(--color-white);color:var(--color-black);font-weight:600;text-align:center}.setting-option.more::after{content:"›";margin-top:-2px;margin-bottom:4px;margin-left:1rem;font-size:2rem;line-height:.4}.setting-option select{width:100%;height:100%;border-radius:0;position:absolute;top:0;left:0;opacity:0}.setting-option input[type=checkbox]{appearance:none;display:block;width:1.625rem;height:1.625rem;border-radius:6px;background:var(--color-white)}.setting-option input[type=checkbox]:checked{border:2px solid var(--color-white);background-color:var(--color-red)}