@import '../includes';

.weights-container {
    height: 100%;
    overflow: auto;
}

.weight-total {
    padding: 1rem;
    background: rgb(0 0 0 / 60%);
    color: $color-white;
    margin-bottom: 1.25rem;
}

.weight-total .weight {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    margin: auto;
    border-radius: 6px;
    font-size: 1.5rem;
    font-weight: 600;
}

.weight-total .weight .modifier {
    padding: 0.125rem 0.5rem;
    margin: 0;
    border-radius: 6px;
    font-size: 0.875rem;
    color: #999;
}

.bar-weight-display {
    width: 100%;
}

.barbell {
    display: none;
    // display: flex;
    align-items: center;
    justify-content: center;
    max-width: 320px;
    width: 100%;
    margin: 0 auto 2rem;
    position: relative;
}

.barbell .out,
.barbell .in,
.barbell .middle {
    background-color: $color-black;
}

.barbell .out {
    width: 20%;
    height: 15px;
}

.barbell .in {
    width: 4%;
    height: 26px;
}

.barbell .middle {
    width: 50%;
    height: 11px;
}

.barbell .weight {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    font-weight: 700;
    font-size: 1.5rem;
}

.group {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    opacity: 0;
    transition: opacity 0.2s;
}

.group {
    opacity: 1;
}

.plate {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 100px;
    margin: 0.5rem 0.5rem;
    border-radius: 50%;
    background-color: #fff;
    font-size: 1.5rem;
    font-weight: bold;
    color: #000;
    position: relative;

    &::after {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: inherit;
        border-radius: 50%;
        z-index: -1;
        right: -3px;
        top: 1px;
        filter: brightness(0.5);
    }
}

.plate.size-55 {
    width: 110px;
    height: 110px;
    background-color: $color-red;
}

.plate.size-45 {
    width: 105px;
    height: 105px;
    background-color: $color-blue;
}

.plate.size-35 {
    width: 100px;
    height: 100px;
    background-color: $color-yellow;
}

.plate.size-25 {
    width: 95px;
    height: 95px;
    background-color: $color-green;
}

.plate.size-20 {
    width: 95px;
    height: 95px;
    background-color: $color-blue;
}

.plate.size-15 {
    width: 90px;
    height: 90px;
    background-color: $color-orange;
}

.plate.size-10 {
    width: 90px;
    height: 90px;
    background-color: $color-white;
}

.plate.size-5 {
    width: 85px;
    height: 85px;
    background-color: $color-blue;
}

.plate.size-2point5 {
    width: 80px;
    height: 80px;
    background-color: $color-green;
}

.plate.size-1point25 {
    width: 70px;
    height: 70px;
    background-color: $color-white;
}

.plate.size-LO,
.plate.message {
    flex-wrap: wrap;
    flex-direction: column;
    max-width: 320px;
    width: 100%;
    height: auto;
    padding: 0.75rem;
    margin: 0 2rem 1rem;
    border-radius: 4px;
    background-color: pink;
    font-size: 1.2rem;
    line-height: 1.5;

    &::after {
        border-radius: 4px;
        top: 3px;
        left: 2px;
    }
}

.plate.message {
    background-color: $color-black-border;
    color: $color-white;
}

.plate.size-LO .message,
.plate.message .copy {
    margin: 0;
}

.plate.size-LO .help,
.plate.message .help {
    width: 100%;
    margin: 0;
    text-align: center;
    font-size: 0.75rem;
}