@import 'global/variables';

:root {
    --color-red: #EA151D;
    --color-blue: #444bfa;
    --color-orange: #EA7715;
    --color-yellow: #CBE619;
    --color-green: #64E619;
    --color-white: #ffffff;
    --color-black: #000000;
    --color-black-border: #222222;
    --color-text-primary: #f1f1f1;
}
  
* {
    box-sizing: border-box;
}

html,
body,
#root {
    height: 100%;
    font-family: sans-serif;
}

body {
    margin: 0;
    background-color: #0c0b11;
    background: linear-gradient(0deg, rgba(12,11,17,1) 0%, rgba(23,21,32,1) 90%);
    color: $color-text-primary;
}