@import '../includes';

.input-container {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.5rem;
    padding: 1rem 0.5rem;
    margin-top: auto;
    background-color: $color-black;
}

.rest-button,
.weight-input {
    display: block;
    width: 7rem;
    height: 3rem;
    padding: 0.5rem 1rem;
    margin: 0;
    border: none;
    outline: none;
    border-radius: 0.4rem;
    background-color: #fff;
    color: $color-black;
    font-size: 1rem;
    font-family: sans-serif;
    text-align: center;
    font-weight: bold;
}

.rest-button {
    display: flex;
    align-items: center;
    width: 4.5rem;
    background: $color-black-border;
    color: $color-white;
    cursor: pointer;
}

.weight-input {
    flex: 100%;
    max-width: 300px;
    font-size: 1.5rem;
}

.weight-input::placeholder {
    font-size: 1rem;
    color: $color-black;
}

.context-buttons {
    display: none;
}

.context-buttons .ok,
.context-buttons .clear {
    display: none;
    width: 3.5rem;
    height: 3rem;
    padding: 0.7rem 0;
    border: 1px solid #333;
    border-radius: 0.4rem;
    background: $color-black;
    color: $color-white;
    text-align: center;
    cursor: pointer;
}

.context-buttons.show-ok,
.context-buttons.show-ok .ok,
.context-buttons.show-clear,
.context-buttons.show-clear .clear {
    display: block;
}

.percent-input {
    appearance: none;
    display: block;
    width: 5.5rem;
    height: 3rem;
    padding: 0.7rem 2rem 0.7rem 1rem;
    margin: 0;
    border: none;
    outline: none;
    border-radius: 0.4rem;
    color: $color-black;
    font-size: 1rem;
    font-family: sans-serif;
    font-weight: bold;
    background: url("/assets/images/chevron-down.svg") right 0.5rem center no-repeat $color-white;
    background-size: 1.25rem;
}

.percent-input optgroup {
    font-size: 0.75rem;
}

.percent-input .simplifed {
    color: $color-red;
}

.simplified-warmup-enabled .percent-input option {
    display: none;
}

.simplified-warmup-enabled .percent-input .simplifed {
    display: block;
}

.clear-button {
    width: 100px;
    padding: 1rem;
    margin: 1rem auto;
    border-radius: 6px;
    background-color: $color-black;
    font-weight: 600;
    text-align: center;
    cursor: pointer;
}

.timer {
    padding: 0px 12px;
    border-radius: 6px;
    background-color:$color-white;
    color:  $color-black;
    font-size: 1.25rem;
    font-weight: 800;
    opacity: 0;
    transition: color 0.5s, border 0.5s, opacity 0.1s;
    position: fixed;
    bottom: 90px;
    left: 8px;

    .rest-timer-active & {
        opacity: 1;
    }

    .rest-timer-warning & {
        background-color: $color-red;
        color:$color-white;
    }
}